import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
/* eslint-disable */
Vue.use(Router);



export default new Router({
    mode: 'history',
    routes: [{
            path: '/',
            name: 'HelloWorld',
            component: HelloWorld
        },
        {
            path: '/',
            name: 'Home',
            component: Home,
        },
        {
            path: '/Life-style/Recipe',
            name: 'Recipe',
            component: () =>
                import ( /* webpackChunkName: 'Life-style' */ '../components/Life-style.vue'),

        },
        {
            path: '/Life-style/Book',
            name: 'Book',
            component: () =>
                import ( /* webpackChunkName: 'Life-style' */ '../components/Life-style.vue'),

        },
        {
            path: '/Life-style/Tool',
            name: 'Tool',
            component: () =>
                import ( /* webpackChunkName: 'Life-style' */ '../components/Life-style.vue'),

        },
        // {
        //     path: '/Community/꽃 색상에 따른 효과',
        //     name: '꽃 색상에 따른 효과',
        //     component: () =>
        //         import ( /* webpackChunkName: 'Community' */ '../components/Community.vue'),
        // },
        // {
        //     path: '/Community/MBTI',
        //     name: 'MBTI',
        //     component: () =>
        //         import ( /* webpackChunkName: 'Community' */ '../components/Community.vue'),
        // },
        // {
        //     path: '/Community/발명품',
        //     name: '발명품',
        //     component: () =>
        //         import ( /* webpackChunkName: 'Community' */ '../components/Community.vue'),
        // },
        {
            path: '/Community/Neologism',
            name: 'Neologism',
            component: () =>
                import ( /* webpackChunkName: 'Community' */ '../components/Neologism.vue'),
        },
        {
            path: '/Memberlounge/게시판',
            name: '게시판',
            component: () =>
                import ( /* webpackChunkName: 'Memberlounge' */ '../components/Memberlounge.vue'),
        },
        {
            path: '/Memberlounge/FAQ',
            name: 'FAQ',
            component: () =>
                import ( /* webpackChunkName: 'Memberlounge' */ '../components/Memberlounge.vue'),
        }
    ]
})


